import { NextRequest, NextResponse } from 'next/server';
import db from '@/lib/db';
import { getToken } from 'next-auth/jwt';
import { createClient } from '@supabase/supabase-js';

// Initialize Supabase client for email/password auth
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL || process.env.SUPABASE_URL;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || process.env.SUPABASE_ANON_KEY;
let supabase: any = null;
if (supabaseUrl && supabaseAnonKey) {
  supabase = createClient(supabaseUrl, supabaseAnonKey);
}

// Helper function to get user by token
async function getUserIdFromRequest(request: NextRequest): Promise<{
  userId: number | null;
  authType: 'google' | 'email' | null;
}> {
  // First try: NextAuth JWT token (Google auth)
  try {
    const token = await getToken({
      req: request,
      secret: process.env.NEXTAUTH_SECRET
    });
    if (token && (token.sub || token.id)) {
      return await getUserIdFromNextAuthToken(token);
    }
  } catch (e) {
    console.log('NextAuth token not found or invalid, trying Bearer token...');
  }

  // Second try: Custom Bearer token (email/password auth)
  const authHeader = request.headers.get('authorization');
  if (authHeader && authHeader.startsWith('Bearer ')) {
    return await getUserIdFromBearerToken(authHeader);
  }
  return {
    userId: null,
    authType: null
  };
}

// Helper for NextAuth tokens
async function getUserIdFromNextAuthToken(token: any): Promise<{
  userId: number | null;
  authType: 'google';
}> {
  let userIdValue = token.id || token.sub;
  if (typeof userIdValue === 'number') {
    userIdValue = userIdValue.toString();
  } else if (typeof userIdValue !== 'string') {
    throw new Error('Invalid userId type from token');
  }
  let userIdNum = parseInt(userIdValue as string, 10);

  // Check if out of range (Google IDs are too large for PostgreSQL INT4)
  if (isNaN(userIdNum) || userIdNum <= 0 || userIdNum > 2147483647 || userIdNum < -2147483648) {
    // Try to find user by email first
    if (token.email) {
      const userByEmail = await db.query('SELECT id FROM users WHERE email = $1', [token.email]);
      if (userByEmail.rows.length > 0) {
        userIdNum = userByEmail.rows[0].id;
        return {
          userId: userIdNum,
          authType: 'google'
        };
      }
    }

    // Try google_id column
    if (userIdValue) {
      try {
        const userLookup = await db.query('SELECT id FROM users WHERE google_id = $1', [userIdValue as string]);
        if (userLookup.rows.length > 0) {
          userIdNum = userLookup.rows[0].id;
          return {
            userId: userIdNum,
            authType: 'google'
          };
        }
      } catch (e) {
        // google_id column might not exist
      }
    }
    throw new Error('User not found');
  }
  return {
    userId: userIdNum,
    authType: 'google'
  };
}

// Helper for Bearer tokens (email/password auth)
async function getUserIdFromBearerToken(authHeader: string): Promise<{
  userId: number | null;
  authType: 'email';
}> {
  const token = authHeader.replace('Bearer ', '');

  // Extract user ID from our mock token format: 'mock-jwt-token-for-user-{userId}'
  const userTokenPattern = /mock-jwt-token-for-user-(\d+)/;
  const match = token.match(userTokenPattern);
  if (match && match[1]) {
    const userId = parseInt(match[1], 10);
    if (!isNaN(userId) && userId > 0) {
      // Verify that this user actually exists in the database
      const {
        rows
      } = await db.query('SELECT id FROM users WHERE id = $1', [userId]);
      if (rows.length > 0) {
        return {
          userId: rows[0].id,
          authType: 'email'
        };
      }
    }
  }

  // Fallback: If we can't extract user from token, try to find a default user
  const {
    rows
  } = await db.query("SELECT id FROM users WHERE email = $1 LIMIT 1", ['john@example.com']);
  if (rows.length > 0) {
    return {
      userId: rows[0].id,
      authType: 'email'
    };
  }
  throw new Error('User not found for Bearer token');
}
export async function DELETE(request: NextRequest, context: {
  params: Promise<{
    productId: string;
  }>;
}) {
  // Wait for params to be resolved in Next.js 13+ App Router
  const params = await context.params;
  const {
    productId
  } = params; // This is the product ID, not cart item ID

  try {
    // Support both NextAuth and Bearer token authentication
    const {
      userId,
      authType
    } = await getUserIdFromRequest(request);

    // If user is authenticated, delete from database
    if (userId && authType) {
      // Validate the parsed userId
      if (userId <= 0) {
        console.error('Invalid userId from token');
        return NextResponse.json({
          success: false,
          error: 'Invalid user authentication'
        }, {
          status: 401
        });
      }
      if (!productId) {
        return NextResponse.json({
          success: false,
          error: 'Product ID is required'
        }, {
          status: 400
        });
      }
      const productIdNum = parseInt(productId);
      if (isNaN(productIdNum) || productIdNum <= 0) {
        return NextResponse.json({
          success: false,
          error: 'Invalid product ID'
        }, {
          status: 400
        });
      }

      // First, check if the cart item exists
      const checkResult = await db.query('SELECT id FROM cart_items WHERE user_id = $1 AND product_id = $2', [userId, productIdNum]);
      if (checkResult.rows.length === 0) {
        return NextResponse.json({
          success: false,
          error: 'Cart item not found'
        }, {
          status: 404
        });
      }
      try {
        const deleteQuery = `
          DELETE FROM cart_items
          WHERE user_id = $1 AND product_id = $2
        `;
        const result = await db.query(deleteQuery, [userId, productIdNum]);
        if (result.error) {
          throw result.error;
        }
        return NextResponse.json({
          success: true,
          message: 'Item removed from cart'
        }, {
          status: 200
        });
      } catch (error) {
        console.error('Error removing item from cart:', error);
        return NextResponse.json({
          success: false,
          error: 'Internal server error'
        }, {
          status: 500
        });
      }
    } else {
      // For unauthenticated users (local cart only), just return success
      // The frontend will handle local cart item removal
      if (!productId) {
        return NextResponse.json({
          success: false,
          error: 'Product ID is required'
        }, {
          status: 400
        });
      }
      return NextResponse.json({
        success: true,
        message: 'Item removed from cart'
      }, {
        status: 200
      });
    }
  } catch (error) {
    console.error('Token verification error:', error);
    // Still allow the operation for unauthenticated users
    return NextResponse.json({
      success: true,
      message: 'Item removed from cart'
    }, {
      status: 200
    });
  }
}